
.l-carousel{position:relative;height:240px;overflow:hidden;margin-left:20px
}
.l-carousel ul{margin-right:-20px
}
.l-carousel ul li{float:left;width:180px;margin-bottom:20px
}
.l-carousel ul li .v-item{width:160px;height:100px;position:relative
}
.l-carousel ul li .v-item .medal{position:absolute;left:0;top:0;display:none;width:40px;height:24px;background:url(http://static.hdslb.com/images/base/icons.png) no-repeat;background-position:-849px -212px;z-index:5;pointer-events:none
}
.l-carousel ul li .v-item .preview{height:100px;background:0;overflow:hidden;border-radius:4px
}
.l-carousel ul li .v-item .preview img{height:100%;opacity:1;margin:0 auto;display:block
}
.l-carousel ul li .v-item .mask{position:absolute;width:100%;height:100%;opacity:0;visibility:hidden;top:0;left:0;background:#000;background:rgba(0,0,0,0.7);filter:alpha(opacity=70) \9;z-index:1;transition:.3s all linear;border-radius:4px
}
.l-carousel ul li .v-item .play-icon{background:url(http://static.hdslb.com/images/v3images/icons2.png) 0 -547px no-repeat;width:40px;height:40px;position:absolute;right:1px;bottom:2px;opacity:.5;filter:alpha(opacity=50);z-index:2;display:none
}
.l-carousel ul li .v-item .info{padding:10px 0 3px;height:18px;overflow:hidden;position:absolute;width:100%;bottom:0;left:0;z-index:1;background:#000;background:url(/images/base/gradient.png) 0 0 repeat-x;background:linear-gradient(transparent, rgba(0,0,0,0.1) 20%, rgba(0,0,0,0.2) 35%, rgba(0,0,0,0.6) 65%, rgba(0,0,0,0.9));border-radius:0 0 4px 4px
}
.l-carousel ul li .v-item .info p{color:#99a2aa;margin:5px 5px 0;opacity:0;transition:.2s all linear
}
.l-carousel ul li .v-item .info .t{height:18px;line-height:18px;overflow:hidden;color:#fff;margin:0 5px;word-break:break-all;word-wrap:break-word
}
.l-carousel ul li .v-item .info .up{margin-top:10px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap
}
.l-carousel ul li .v-item:hover .mask{visibility:visible;opacity:1;transition:none
}
.l-carousel ul li .v-item:hover .play-icon{display:none
}
.l-carousel ul li .v-item:hover .info{padding:0;height:100%;background:0
}
.l-carousel ul li .v-item:hover .info p{opacity:1;transition:.3s all linear
}
.l-carousel ul li .v-item:hover .t{margin-top:10px;height:36px
}
.l-carousel .prev{position:absolute;left:0;border-radius:0 2px 2px 0;background-position:6px -1211px;padding:13px 5px 13px 10px;background-color:rgba(0,0,0,0.6);background-image:url(http://static.hdslb.com/images/v3images/icons2.png);background-repeat:no-repeat;width:20px;top:50%;margin-top:-30px;cursor:pointer;font-size:12px;color:#fff;z-index:99;text-align:center;display:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.l-carousel .next{position:absolute;right:0;border-radius:0 2px 2px 0;background-position:6px -1211px;padding:13px 5px 13px 10px;background-color:rgba(0,0,0,0.6);background-image:url(http://static.hdslb.com/images/v3images/icons2.png);background-repeat:no-repeat;width:20px;top:50%;margin-top:-30px;cursor:pointer;font-size:12px;color:#fff;z-index:99;text-align:center;display:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.l-carousel:hover .prev,.l-carousel:hover .next{display:block
}



