
.t-carousel{position:relative;width:440px;height:220px;overflow:hidden
}
.t-carousel ul.carousel{width:500%;height:100%;transition:transform 0.3s cubic-bezier(0.86, 0, 0.07, 1);transform:translate3d(0, 0, 0)
}
.t-carousel ul.carousel li{position:relative;width:440px;height:220px;float:left;overflow:hidden
}
.t-carousel ul.carousel li a{display:inline-block;width:100%;height:80%;color:white
}
.t-carousel ul.carousel li a.show-info{position:absolute;top:84%;left:20px;width:auto;line-height:35px;font-size:14px;height:35px;cursor:pointer
}
.t-carousel ul.carousel li a.show-info:hover{text-shadow:0 0 3px #fff
}
.t-carousel .more-topic{position:absolute;right:15px;bottom:35px;color:#fff;background:#444;background:rgba(0,0,0,0.64);width:50px;height:24px;line-height:24px;text-align:center;border-radius:4px;opacity:0;visibility:hidden;transition:.2s all linear
}
.t-carousel .more-topic:hover{text-shadow:0 0 3px #fff;color:#fff
}
.t-carousel:hover .more-topic{opacity:1;visibility:visible
}
.t-carousel ul.slider-bar{position:absolute;right:5px;bottom:5px;overflow:hidden;padding:2px 5px
}
.t-carousel ul.slider-bar li{float:left;cursor:pointer;width:18px;height:18px;margin:2px 2px;background:url(http://static.hdslb.com/images/base/icons.png) -855px -790px no-repeat
}
.t-carousel ul.slider-bar li:hover{background-position:-919px -790px
}
.t-carousel ul.slider-bar li.on{background-position:-855px -727px
}
.t-carousel ul.slider-bar li.on:hover{background-position:-855px -727px
}



