
.top-nav{color:#222;position:relative;z-index:10000
}
.top-nav .bg{position:absolute;top:0;left:0;width:100%;height:42px;overflow:hidden;background-color:rgba(255,255,255,0.4);box-shadow:rgba(0,0,0,0.1) 0 1px 2px
}
.top-nav .bg .bg-ma{position:absolute;height:62px;padding:0 20px;top:-10px;left:-20px;width:100%;-webkit-filter:blur(5px);-ms-filter:blur(5px);filter:blur(5px);filter:progid:DXImageTransform.Microsoft.Blur(PixelRadius=5,MakeShadow=false);z-index:50;background:white no-repeat center 0;background-position:center 0 !important;background-repeat:no-repeat
}
.top-nav .bg .bg-model{position:absolute;top:0;left:0;width:100%;height:42px;background-color:rgba(255,255,255,0.4);box-shadow:rgba(0,0,0,0.1) 0 1px 2px;z-index:99
}
.top-nav .nav-content{width:1160px;height:42px;margin:0 auto;position:relative;text-align:left;z-index:10000;font-size:13px
}
.top-nav .nav-content .nav{float:left;height:42px
}
.top-nav .nav-content .nav li{position:relative;float:left;line-height:42px;transition:all 0.3s ease
}
.top-nav .nav-content .nav li .show-content{display:none;position:absolute !important;top:42px;width:466px;height:256px;border-radius:0 0 4px 4px;box-shadow:1px 1px 3px rgba(0,0,0,0.4);position:relative;background:#fff;text-align:left;transition:all 0.3s ease
}
.top-nav .nav-content .nav li .show-content .left{width:265px;margin:12px 0 0 0;float:left
}
.top-nav .nav-content .nav li .show-content .left .banner{width:240px;height:128px;margin-left:12px
}
.top-nav .nav-content .nav li .show-content .left .banner a{position:relative;padding:0
}
.top-nav .nav-content .nav li .show-content .left .banner a img{width:240px;height:128px;border-radius:4px
}
.top-nav .nav-content .nav li .show-content .left .banner a span{width:220px;padding:0 10px;border-radius:4px;position:absolute;bottom:0px;left:0px;font-size:12px;line-height:30px;text-shadow:1px 1px 1px #000;color:#fff;background:url(http://static.biligame.net/biligametop/img/shadow.png) repeat-x
}
.top-nav .nav-content .nav li .show-content .left .brief{margin-top:6px
}
.top-nav .nav-content .nav li .show-content .left .brief a{position:relative;width:64px;height:104px;padding:6px 12px 0;display:block;float:left;overflow:hidden
}
.top-nav .nav-content .nav li .show-content .left .brief a:hover{background-color:#e5e9ef
}
.top-nav .nav-content .nav li .show-content .left .brief a img{width:64px;height:64px;border-radius:12px
}
.top-nav .nav-content .nav li .show-content .left .brief a span{position:absolute;top:72px;left:17px;width:4em;height:14px;margin:8px auto;display:block;font-size:14px;line-height:1;text-align:center;color:#222;overflow:hidden
}
.top-nav .nav-content .nav li .show-content .right{width:201px;min-height:234px;margin:12px 0 0 0;float:left;background:url(http://static.biligame.net/biligametop/img/line.png) no-repeat
}
.top-nav .nav-content .nav li .show-content .right .bbs{margin-top:-4px;margin-right:12px;display:inline-block;float:right;font-size:12px;color:#00a1d6
}
.top-nav .nav-content .nav li .show-content .right .all{padding:35px 0 0 6px;background:url(http://static.biligame.net/biligametop/img/title.png) no-repeat
}
.top-nav .nav-content .nav li .show-content .right .all a{width:163px;padding-left:16px;display:block;font-size:12px;line-height:28px;color:#222
}
.top-nav .nav-content .nav li .show-content .right .all a:hover{background-color:#e5e9ef
}
.top-nav .nav-content .nav li .show-content .imgdiv{width:220px;height:220px;display:block;position:absolute;bottom:0;left:442px
}
.top-nav .nav-content .nav li .live-box{position:relative;padding:20px 0 0px 20px;width:504px;background:#fff;border-radius:0 0 4px 4px;box-shadow:1px 1px 3px rgba(0,0,0,0.4);font-family:"微软雅黑"
}
.top-nav .nav-content .nav li .live-box .title{padding:0;margin:0;font-size:14px;line-height:15px;color:#ea759f
}
.top-nav .nav-content .nav li .live-box .live-lf{float:left;width:254px
}
.top-nav .nav-content .nav li .live-box .live-lf .lf-list{margin:6px 0 0;border-right:1px solid #eaeaea
}
.top-nav .nav-content .nav li .live-box .live-lf .lf-list .lf-item{position:relative;display:inline-block;position:relative;margin:10px 17px 0 0;width:64px;height:100px;padding:0
}
.top-nav .nav-content .nav li .live-box .live-lf .lf-list .lf-item img{width:64px;height:64px;border-radius:50%;overflow:hidden
}
.top-nav .nav-content .nav li .live-box .live-lf .lf-list .lf-item i{display:none;position:absolute;top:0;left:0;width:64px;height:64px;border-radius:50%;overflow:hidden;background:rgba(0,0,0,0.5);font-style:normal;font-size:15px;color:#fff;text-indent:22px;line-height:64px;font-weight:700;cursor:pointer;animation:scale-in-ease cubic-bezier(0.22, 0.58, 0.12, 0.98) 0.5s
}
.top-nav .nav-content .nav li .live-box .live-lf .lf-list .lf-item i:before{position:absolute;left:8px;top:27px;content:"";width:12px;height:12px;background:#ff699e;border-radius:50%;overflow:hidden
}
@keyframes scale-in-ease{
0%{opacity:0;transform:scale(0)
}
50%{transform:scale(1)
}
100%{opacity:1;transform:scale(1)
}
}
.top-nav .nav-content .nav li .live-box .live-lf .lf-list .lf-item p{position:absolute;top:60px;padding:0;margin:3px 0 0;text-align:center;color:#000;width:100%;white-space:nowrap;text-overflow:ellipsis;overflow:hidden;font-size:12px
}
.top-nav .nav-content .nav li .live-box .live-lf .lf-list .lf-item:hover i{display:block
}
.top-nav .nav-content .nav li .live-box .live-lr{width:250px;display:inline-block
}
.top-nav .nav-content .nav li .live-box .live-lr .banner{margin-top:16px
}
.top-nav .nav-content .nav li .live-box .live-lr .banner a{display:block;padding:10px 21px;width:208px;height:80px
}
.top-nav .nav-content .nav li .live-box .live-lr .banner a:hover{background-color:#eaeaea
}
.top-nav .nav-content .nav li:hover .show-content{display:block
}
.top-nav .nav-content .nav li:first-child{padding:0;background-image:url("http://static.hdslb.com/images/base/icons.png");background-repeat:no-repeat;background-position:-910px -74px;width:70px;margin-left:-10px
}
.top-nav .nav-content .nav li:first-child a{width:40px;padding-left:32px
}
.top-nav .nav-content .nav li a{display:inline-block;padding:0 15px;height:100%
}
.top-nav .nav-content .user-box{float:right;height:42px
}
.top-nav .nav-content .user-box ul{padding-left:10px;float:left;height:42px
}
.top-nav .nav-content .user-box ul li{float:left;line-height:42px;padding:0 10px;transition:all 0.3s ease
}
.top-nav .nav-content .user-box ul li:last-child{padding-right:0px
}
.top-nav .nav-content .user-box ul li a{display:inline-block;width:100%;height:100%
}
.top-nav .nav-content .user-box ul li a.i-link{margin:0;padding:0;text-align:center;width:58px;position:relative;z-index:10;background:url("http://static.hdslb.com/images/base/b-post.png") center center no-repeat;height:48px;border-radius:0 0 5px 5px;color:#fff !important;font-size:14px
}
.top-nav .nav-content .user-box ul li a.i-link:hover{opacity:0.9
}
.top-nav .nav-content .user-box ul .s-line{float:left;display:inline-block;border-left:1px solid #222;height:12px;margin-top:16px;vertical-align:top
}
.top-nav .nav-content .user-box ul .s-menu{position:absolute;display:block;top:42px;right:0;z-index:15;width:216px;border:0;transition:.2s;border-radius:0 0 4px 4px;background-color:#fff;box-shadow:rgba(0,0,0,0.16) 0 2px 4px;overflow:hidden;height:64px;padding-left:0;opacity:0;visibility:hidden
}
.top-nav .nav-content .user-box ul .s-menu li{float:left;width:72px;height:64px;transition:.1s;padding:0
}
.top-nav .nav-content .user-box ul .s-menu li a{display:block;color:#f25d8e;padding:14px 12px 0 12px;line-height:1;width:52px;height:50px;font-size:12px
}
.top-nav .nav-content .user-box ul .s-menu li a i{display:block;width:20px;height:20px;margin:0 auto 8px;background:url("http://static.hdslb.com/images/base/icons.png") no-repeat
}
.top-nav .nav-content .user-box ul .s-menu li a i.b-icon-vp{background-position:-471px -919px
}
.top-nav .nav-content .user-box ul .s-menu li a i.b-icon-vm{background-position:-471px -982px
}
.top-nav .nav-content .user-box ul .s-menu li a i.b-icon-vc{background-position:-471px -1753px
}
.top-nav .nav-content .user-box ul .s-menu li:hover{background-color:#e5e9ef
}
.top-nav .nav-content li:hover{background-color:rgba(255,255,255,0.2)
}
.top-nav .nav-content li.none:hover{background:none
}
.top-nav .nav-content li.none:hover .s-menu{opacity:1;visibility:visible
}
@media screen and (max-width: 1400px){
.top-nav .nav-content{width:980px
}
}



